.footer {
    background-color: #ece8e4;
    border-top: 4px solid #b9b7b7;

    .footer__item {
        padding: 40px 0 0 0;

        .footer__item__title {
            color: #629c49;
            font-size: 16px;
            font-weight: 600;
            margin-bottom: 15px;
            border-bottom: 1px solid #a9d895;
            padding-bottom: 10px;
        }

        .footer__item__link {
            display: block;
            color: #333;
            margin: 10px 0;
            font-size: 13px;
        }

        .footer__item__desc {
            font-size: 12px;
            margin-bottom: 10px;
        }

        .footer__item__logo {
            margin-bottom: 10px;
        }
    }

    .footer__bot {
        border-top: 2px solid #ccc;
        padding: 10px;
        text-align: center;
        font-size: 11px;

        a {
            color: #629c49;
        }
    }
}